body
{
	font-family: "Helvetica";
}

.article_component 
{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 100%;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.articles 
{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	width: 100%;
}

.articles > a 
{
	margin: 5px;
}

.article_header 
{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 100%;
	height: 100px;
}

.article_header_left, .article_header_center, .article_header_right 
{
	margin: 20px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	width: 50%;
	height: 100%;
}

.article_header_left 
{
	-ms-flex-align: start;
	    align-items: flex-start;
}

.article_header_center 
{
	-ms-flex-align: center;
	    align-items: center;
}

.article_header_right 
{
	-ms-flex-align: end;
	    align-items: flex-end;
}

.article_header_right > span, .article_header_center > span 
{
	font-size: 14pt;
	font-weight: 600;
}

.article_content 
{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	width: 100%;
}

.article_content 
{
	font-size: 14pt;
}

.article_footer 
{
	margin: 20px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.article_footer > button 
{
	width: 150px;
	height: 50px;
	font-size: 12pt;
	font-family: Helvetica;
	font-weight: 600;
}





